<template>
  <div class="examples">
    <div class="examples-header">
      <a href="https://github.com/zenquan/vue-3d-app" class="title">三维模型库</a>
    </div>
    <div class="examples-content">
      <ul class="examples-menu">
        <li class="examples-menu-title">机械制图习题集</li>
        <router-link to="/">第五章 轴测图</router-link>
        <li class="examples-menu-item" v-for="item of menus" :key="item.title">
          <router-link :to="item.router">{{item.title}}</router-link>
        </li>
      </ul>
      <div class="examples-pages">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      menus: [
        { index: 0, router: "/ch5281", title: "5-28-1" },
        { index: 1, router: "/ch5282", title: "5-28-2" },
        { index: 2, router: "/ch5283", title: "5-28-3" },
        { index: 3, router: "/ch5284", title: "5-28-4" },
        { index: 4, router: "/ch5285", title: "5-28-5" },
        { index: 5, router: "/ch5286", title: "5-28-6" },
        { index: 6, router: "/ch5291", title: "5-29-1" },
        { index: 7, router: "/ch5292", title: "5-29-2" },
        { index: 8, router: "/ch5293", title: "5-29-3" },
        { index: 9, router: "/ch5294", title: "5-29-4" }
      ]
    };
  }
};
</script>
<style>
* {
  margin: 0;
  border: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,
ol,
li {
  list-style: none;
}
a {
  color: #657180;
  text-decoration: none;
}
html,
body,
.examples {
  width: 100%;
  height: 100%;
  color: #657180;
}
body {
  background: #eee;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", SimSun, sans-serif;
  background: #fff;
}
.examples-header {
  position: absolute;
  top: 0;
  width: 100%;
  background: #f3f6fb;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  border-bottom: solid 1px #dfe2e7;
  padding: 15px 15px 15px 30px;
  line-height: 20px;
}
.examples-header .title {
  font-weight: bold;
  font-size: 18px;
  display: inline-block;
}
.examples-header .github {
  color: #39f;
  display: inline-block;
  font-size: 12px;
  float: right;
}
.examples-content {
  width: 100%;
  height: 100%;
  padding-top: 52px;
  overflow: hidden;
}
.examples-menu {
  float: left;
  width: 230px;
  height: 100%;
  border-right: solid 1px #dfe2e7;
  padding: 10px 30px;
}
.examples-menu .examples-menu-item {
  display: block;
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  cursor: pointer;
}
.examples-menu .examples-menu-item > a {
  display: block;
  width: 100%;
  height: 100%;
}
.examples-menu .examples-menu-title {
  display: block;
  width: 100%;
  padding: 6px 0;
  margin-top: 10px;
  font-size: 14px;
  font-weight: bold;
}
.examples-menu .examples-menu-item:hover > a {
  color: #39f;
}
.examples-pages {
  height: 100%;
  overflow: hidden;
}
.example-loading {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}
.example-loading::before {
  content: "";
  border-radius: 100%;
  margin: 2px;
  border: 3px solid #20a0ff;
  border-bottom-color: transparent;
  height: 36px;
  width: 36px;
  display: inline-block;
  animation: loading-rotate 0.75s 0s linear infinite;
  vertical-align: middle;
}
.example-loading::after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 0;
  vertical-align: middle;
}
@keyframes loading-rotate {
  0% {
    transform: rotate(0) scale(1);
  }
  50% {
    transform: rotate(180deg) scale(0.6);
  }
  100% {
    transform: rotate(360deg) scale(1);
  }
}
</style>


